.elCont{
   
    height: 100%;
    width: 100vw;
    max-width: 100%;
    overflow: hidden;
    
}
.AllContentContainer{
   
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    height: 100%;
    width: 100vw;
    max-width: 100%;
    position: absolute;
    top: 0;
    z-index: 1;
     
}

.TextContainer{
     
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
   
    max-width: 100%;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    
    font-size: 8rem;
    color: rgb(235, 235, 235); 
    font-family: 'SBAggroB';
    text-align: center;
}
.PassContainer{
    position: fixed;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    max-width: 100%;
    align-items: center;
    justify-content: center;
}
 
@media only screen and (max-width: 600px){
    .TextContainer{
     
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        height: 100vh;
        width: 100vw;
       
        max-width: 100%;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        
        font-size: 3rem;
        color: rgb(235, 235, 235); 
        font-family: 'SBAggroB';
        text-align: center;
    }
}

 