.FlexContainer {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 95vw;
    height: 60vh;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
}

.ColumnItemContainer {
    width: 45vw;
    height: 100%;
    text-align: center;
    margin: 5px;
}
.ColumnItems {
    width: 100%;
    height: 100%;
    object-fit: cover;
    overflow: hidden;
    border-radius: 30px;
}
.TextColumnFlexContainer{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}
.TextColumnItemsTitle{
    width:70%;
    font-size: 2rem;
    color: #1D1D1F;
    font-family: 'SBAggroB';
    line-height: 1.5;
    text-align: left;
    margin: 10px;
}
.TextColumnItemsContent{
    width:90%;
    
    font-size: 1.2rem;
    font-weight: 600;
    color: #8A8A8F;
    font-family: 'Pretendard-Regular';
    
    text-align: left;
    margin: 10px;
}
.ItemImage{
    filter: brightness(50%);
    transition: all .5s;
    pointer-events: none
}
.TextOverImageLeft {
    position: absolute;
    width: 40%;
    left: 5%;
    bottom: 5%;
    font-size: 3rem;
    color: rgb(235, 235, 235);
    font-family: 'SBAggroB';
    text-align: left;
}
.TextOverImageRight {
    position: absolute;
    width: 40%;
    Right: 5%;
    bottom: 5%;
    font-size: 3rem;
    color: rgb(235, 235, 235);
    font-family: 'SBAggroB';
    text-align: left;
}
@media only screen and (max-width: 600px) {
    .FlexContainer {
        display: flex;
        position: relative;
        flex-wrap: nowrap;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 90vw;
        height: 80vh;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 20px;
    }
    .ColumnItemContainer {
        width: 100%;
        height: 100%;
        text-align: center;

    }
    .TextColumnItemsTitle{
        width:90%;
        font-size: 1.6rem;
        color: #1D1D1F;
        font-family: 'SBAggroB';
        line-height: 1.2;
        text-align: left;
        
    }
    .TextOverImageLeft {
        position: absolute;
        width: 80%;
        left: 5%;
        bottom: 5%;
        font-size: 3rem;
        color: rgb(235, 235, 235);
        font-family: 'SBAggroB';
        text-align: left;
    }
    .TextOverImageRight {
        position: absolute;
        width: 80%;
        Right: 5%;
        bottom: 5%;
        font-size: 3rem;
        color: rgb(235, 235, 235);
        font-family: 'SBAggroB';
        text-align: left;
    }

    
}
